import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PaisAmbiente } from '@app/shared/modelos/enums/paisAmbiente.enum';
import { environment } from '@env/environment';

@Component({
  selector: 'app-menu-izquierdo',
  templateUrl: './menu-izquierdo.component.html',
  styleUrls: ['./menu-izquierdo.component.scss'],
})
export class MenuIzquierdoComponent implements OnInit {
  @Input() abrirMenu: boolean;
  @Input() isAdmin: boolean;
  opcionSeleccionada: any = {
    home: false,
    lista: false,
    notificaciones: false,
  };
  pais: PaisAmbiente;
  PaisAmbiente = PaisAmbiente;

  constructor(private router: Router) {}

  ngOnInit() {
    this.router.events.subscribe((val: any) => {
      this.obtenerSeccionSeleccionada(val.url);
    });
    this.pais = environment.envCountry as PaisAmbiente;
  }

  obtenerSeccionSeleccionada(url: string) {
    if (url) {
      if (url.includes('invitaciones-empresa')) {
        this.seleccionarOption('lista');
      } else if (url.includes('notificaciones')) {
        this.seleccionarOption('notificaciones');
      } else {
        this.seleccionarOption('home');
      }
    }
  }

  seleccionarOption(nombre: string) {
    this.opcionSeleccionada = {};
    this.opcionSeleccionada[nombre] = true;
  }
}
