import { Component, OnInit } from '@angular/core';
import {
  AbstractControl,
  FormBuilder,
  FormGroup,
  Validators,
} from '@angular/forms';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { Store } from '@ngrx/store';
import { first } from 'rxjs/operators';
import { RegistroEmpresaColombia } from '@app/modulos/comercial/modelo/registroEmpresa.model';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { TipoIdentificacionEmpresa } from '@app/modulos/comercial/modelo/tipo-identificacion-empresa.enum';
import { identificacionColombiaValidador } from '@app/modulos/comercial/validador/registro-empresa.validador';
import { validarTextoSinNumero } from '@app/modulos/comercial/validador/expresionesRegulares';
import { ApiService } from '@core/servicios/api.service';

@Component({
  selector: 'app-registro-empresa-colombia',
  templateUrl: './registro-empresa-colombia.component.html',
  styleUrls: ['./registro-empresa-colombia.component.scss'],
})
export class RegistroEmpresaColombiaComponent implements OnInit {
  formularioEmpresa: FormGroup;
  userSub: string;
  cargando = false;
  correo: string;
  TipoIdentificacionEmpresa = TipoIdentificacionEmpresa;

  constructor(
    private fb: FormBuilder,
    private apiService: ApiService,
    private customModal: CustomModalService,
    private store: Store<any>
  ) {}

  ngOnInit() {
    this.formularioEmpresa = this.crearFormulario(this.fb);
  }

  crearFormulario(fb: FormBuilder): FormGroup {
    return fb.group(
      {
        identificacion: fb.control('', [Validators.required]),
        tipoIdEmpresa: fb.control('', [Validators.required]),
        totalEmpleados: fb.control('', [
          Validators.required,
          Validators.pattern('^[0-9]*'),
          Validators.min(1),
          Validators.max(2147483647),
          Validators.maxLength(10),
        ]),
        nombre: fb.control('', [
          Validators.required,
          Validators.maxLength(301),
          Validators.minLength(2),
        ]),
        nombreAdministrador: fb.control('', [
          ...validarTextoSinNumero,
          Validators.required,
          Validators.maxLength(301),
          Validators.minLength(2),
        ]),
        correoAdministrador: fb.control('', [
          Validators.required,
          Validators.maxLength(301),
          Validators.minLength(2),
          this.validarEmailCampo,
        ]),
      },
      {
        validator: identificacionColombiaValidador(
          'tipoIdEmpresa',
          'identificacion'
        ),
      }
    );
  }

  enviarFormulario(): void {
    if (this.formularioEmpresa.valid) {
      this.store
        .select('usuario', 'sub')
        .pipe(first())
        .subscribe((value) => {
          this.userSub = value;
          this.registrarEmpresa();
        });
    }
  }

  registrarEmpresa() {
    this.correo = this.formularioEmpresa.get('correoAdministrador').value;
    this.cargando = true;
    const result: RegistroEmpresaColombia = {
      ...this.formularioEmpresa.value,
      codigoPais: 'CO',
      idUsuario: this.userSub,
      correoAdministrador: this.correo.toLowerCase(),
    };
    this.apiService
      .post('empresa/sin_contrato/registrar', result)
      .then(() => {
        this.customModal.abrirModal(CustomModalType.SUCCESS, {
          mensaje: 'Los cambios se han realizado con Ã©xito.',
        });
        this.formularioEmpresa.reset();
        this.cargando = false;
      })
      .catch((e) => {
        this.cargando = false;

        switch (e.data.codigo) {
          case 'empresa.identificacion.duplicada':
            this.customModal.abrirModal(CustomModalType.ERROR, {
              mensaje: 'Esta identificacion no se encuentra disponible.',
            });
            break;
          case 'empresa.identificacion.salesforce.no-existe':
            this.customModal.abrirModal(CustomModalType.ERROR, {
              mensaje: 'La empresa no se encuentra registrada en salesforce',
            });
            break;
          default:
            this.customModal.abrirModal(CustomModalType.ERROR, {
              mensaje: 'Sucedio un error inesperado en la aplicaciÃ³n.',
            });
        }
      });
  }

  get identificacion() {
    return this.formularioEmpresa.get('identificacion');
  }

  get tipoIdEmpresa() {
    return this.formularioEmpresa.get('tipoIdEmpresa');
  }

  get totalEmpleados() {
    return this.formularioEmpresa.get('totalEmpleados');
  }

  get nombre(): any {
    return this.formularioEmpresa.get('nombre');
  }

  get nombreAdministrador(): any {
    return this.formularioEmpresa.get('nombreAdministrador');
  }

  get correoAdministrador(): any {
    return this.formularioEmpresa.get('correoAdministrador');
  }

  validarEnBlur(control: AbstractControl) {
    const valor: string = control.value;
    control.setValue(valor.trim());
    control.markAsDirty();
  }

  validarEnBlurYHacerTrim(control: AbstractControl) {
    const valor: string = control.value;
    control.setValue(valor.trim());
    control.markAsDirty();
  }

  validarEmailCampo(control: AbstractControl): { pattern: boolean } {
    const regExpr = new RegExp(
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );
    const emailValue = control.value
      ? regExpr.test(String(control.value).toLowerCase())
      : true;
    if (!emailValue) {
      return { pattern: true };
    }
    return null;
  }
}
