import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { environment } from '@env/environment';
import {
  humanizeBytes,
  UploaderOptions,
  UploadFile,
  UploadInput,
  UploadOutput,
} from 'ngx-uploader';
import { Subscription } from 'rxjs';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { RespuestaEnvioNotificaciones } from '@app/modulos/comercial/modelo/notificacion-dto.model';
import { ApiService } from '@core/servicios/api.service';

@Component({
  selector: 'app-subir-layout',
  templateUrl: './subir-layout.component.html',
  styleUrls: ['./subir-layout.component.scss'],
})
export class SubirLayoutComponent implements OnInit, OnDestroy {
  uuidEmpresa: string;
  _disabled: boolean;

  nombre: string;
  files: UploadFile;
  options: UploaderOptions;
  empresaSubscription: Subscription;
  uploadInput: EventEmitter<UploadInput>;
  cargando = false;
  humanizeBytes: (bytes: number) => string;

  @Input() disabled: boolean;
  @Input() endPoint = 'enviar_mensajes';
  @Output() enviarEstadoLayout = new EventEmitter();
  @ViewChild('file', { static: true }) file: ElementRef;

  constructor(
    private apiService: ApiService,
    private customModalService: CustomModalService
  ) {
    this.uploadInput = new EventEmitter<UploadInput>();
    this.humanizeBytes = humanizeBytes;
  }

  ngOnInit() {
    this.options = {
      concurrency: 0,
      allowedContentTypes: [
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        'application/msexcel',
        'application/vnd.ms-excel',
      ],
    };
  }

  ngOnDestroy() {
    if (this.empresaSubscription) {
      this.empresaSubscription.unsubscribe();
    }
  }

  onUploadOutput(output: UploadOutput): void {
    this.validarTipoDeArchivo(output);
    if (output.type === 'addedToQueue' && typeof output.file !== 'undefined') {
      setTimeout(() => {
        if (output.file.size > 635000) {
          this.eliminarDocumento();
          this.customModalService.abrirModal(CustomModalType.ERROR, {
            mensaje: 'Recuerda cargar un archivo menor a 635 kb',
          });
        } else {
          this.files = output.file;
          this.nombre = output.file.name;
        }
      }, 200);
    } else if (
      output.type === 'uploading' &&
      typeof output.file !== 'undefined'
    ) {
      this.cargando = true;
      this.enviarEstadoLayout.emit({
        estado: false,
        estadoSubidaDocumento: output.file.progress.data.percentage,
      });
    } else if (output.type === 'done') {
      this._disabled = false;
      this.cargando = false;
      switch (output.file.responseStatus) {
        case 200:
          this.cargaExcelExitosa(output.file.response);
          break;
        case 400:
          this.mostrarModalErrorValidacion(output.file.response);
          break;
        case 0:
          this.mostrarModalErrorPersonalizado(
            'No pudimos enviar la notificaciÃ³n, recuerda cargar un archivo mÃ¡ximo con 20.000 registros'
          );
          break;
        default:
          this.mostrarModalError();
      }
      this.eliminarDocumento();
    }
  }

  private validarTipoDeArchivo(output: UploadOutput) {
    if (
      typeof output.file !== 'undefined' &&
      !this.options.allowedContentTypes.some(
        (value) => value === output.file.type
      )
    ) {
      this.customModalService.abrirModal(CustomModalType.ERROR, {
        mensaje:
          'El archivo no es vÃ¡lido. Solo puedes subir documentos tipo XLS Ã³ XLSX',
      });
    }
  }

  eliminarDocumento() {
    this.uploadInput.emit({ type: 'removeAll' });
    this.uploadInput.emit({ type: 'cancelAll' });
    this.files = null;
    this.file.nativeElement.value = null;
    this.nombre = null;
  }

  startUpload(body: any): void {
    this._disabled = true;
    this.cargando = true;
    this.apiService.getCurrentSesion().then((sesion) => {
      const accessToken = sesion.getAccessToken();
      const jwt = accessToken.getJwtToken();
      const event: UploadInput = {
        type: 'uploadFile',
        file: this.files,
        url: environment.endPointNotificacion + this.endPoint,
        method: 'POST',
        headers: { ['Authorization']: 'Bearer ' + jwt },
        data: body,
      };
      this.uploadInput.emit(event);
    });
  }

  mostrarModalError() {
    const dialog = this.customModalService.abrirModal(CustomModalType.ERROR, {
      mensaje: 'La notificaciÃ³n no pudo ser enviada',
    });
    dialog.afterClosed().subscribe(() => {
      this._disabled = false;
    });
  }

  mostrarModalErrorPersonalizado(mensaje: string) {
    const dialog = this.customModalService.abrirModal(CustomModalType.ERROR, {
      mensaje,
    });
    dialog.afterClosed().subscribe(() => {
      this._disabled = false;
    });
  }

  mostrarModalErrorValidacion(errorValidacion: {
    mensaje: string;
    campos: string[];
  }) {
    const dialog = this.customModalService.abrirModal(CustomModalType.ERROR, {
      mensaje: ['La notificaciÃ³n no pudo ser enviada', errorValidacion.mensaje],
    });
    dialog.afterClosed().subscribe(() => {
      this._disabled = false;
    });
  }

  cargaExcelExitosa(respuesta: RespuestaEnvioNotificaciones) {
    let dialog = this.customModalService.abrirModal(CustomModalType.SUCCESS, {
      mensaje: respuesta.mensaje,
    });
    if (dialog) {
      dialog.afterClosed().subscribe(() => {
        this._disabled = false;
        this.enviarEstadoLayout.emit('modalCerrado');
      });
    }
  }
}
