import { Component, OnInit, ViewChild } from '@angular/core';
import {
  AbstractControl,
  FormBuilder,
  FormGroup,
  Validators,
} from '@angular/forms';
import { Store } from '@ngrx/store';
import { first } from 'rxjs/operators';
import { TipoNotificacion } from '@app/modulos/comercial/modelo/tipo-notificacion.enum';
import {
  ArchivoMensajesDTO,
  RespuestaEnvioNotificaciones,
} from '@app/modulos/comercial/modelo/notificacion-dto.model';
import { SubirLayoutComponent } from '@app/modulos/comercial/componentes/subir-layout/subir-layout.component';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '@env/environment';
import { Accion } from '@app/modulos/comercial/modelo/accion.model';
import { ApiService } from '@core/servicios/api.service';

@Component({
  selector: 'app-notificar',
  templateUrl: './notificar.component.html',
  styleUrls: ['./notificar.component.scss'],
})
export class NotificarComponent implements OnInit {
  @ViewChild('cargaArchivo', { static: true })
  cargaArchivo: SubirLayoutComponent;

  userSub: string;
  cargando = false;
  formulario: FormGroup;
  TipoNotificacion = TipoNotificacion;
  limitesMensaje = {
    SMS: 160,
    PUSH: 240,
    CORREO: 500,
  };
  acciones: [Accion];

  constructor(
    private fb: FormBuilder,
    private store: Store<any>,
    private http: HttpClient,
    private apiService: ApiService
  ) {}

  ngOnInit() {
    this.formulario = this.crearFormulario(this.fb);
    this.store
      .select('usuario', 'sub')
      .pipe(first())
      .subscribe((value) => {
        this.userSub = value;
      });
    this.obtenerAcciones();
  }

  crearFormulario(fb: FormBuilder): FormGroup {
    return fb.group({
      titulo: fb.control('', [
        Validators.required,
        Validators.maxLength(65),
        Validators.minLength(2),
      ]),
      tipo: fb.control('', [Validators.required]),
      campana: fb.control('', [
        Validators.required,
        Validators.maxLength(65),
        Validators.minLength(2),
      ]),
      accion: fb.control(''),
      descripcion: fb.control('', [
        Validators.required,
        Validators.maxLength(160),
        Validators.minLength(5),
      ]),
      checkTodos: fb.control(false, [Validators.required]),
    });
  }

  enviarFormulario(): void {
    if (this.formulario.valid) {
      const datosFormulario = this.formulario.value;
      const body: ArchivoMensajesDTO = {
        idCampania: datosFormulario.campana,
        titulo: datosFormulario.titulo,
        mensaje: datosFormulario.descripcion,
        medios: datosFormulario.tipo,
        todos: datosFormulario.checkTodos,
        accion: datosFormulario.accion,
        idUsuario: this.userSub,
      };
      if (!this.checkTodos.value && this.cargaArchivo.files) {
        this.cargaArchivo.startUpload(body);
      } else {
        this.enviarPeticionTodos(body);
      }
    }
  }

  get titulo() {
    return this.formulario.get('titulo');
  }

  get tipo() {
    return this.formulario.get('tipo');
  }

  get campana() {
    return this.formulario.get('campana');
  }

  get accion() {
    return this.formulario.get('accion');
  }

  get descripcion(): any {
    return this.formulario.get('descripcion');
  }

  get checkTodos(): any {
    return this.formulario.get('checkTodos');
  }

  validarEnBlur(control: AbstractControl) {
    const valor: string = control.value;
    control.setValue(valor.trim());
    control.markAsDirty();
  }

  validarEnBlurYHacerTrim(control: AbstractControl) {
    const valor: string = control.value;
    control.setValue(valor.trim());
    control.markAsDirty();
  }

  errorArchivoRequerido() {
    return !this.checkTodos.value && !this.cargaArchivo.files;
  }

  seleccionCambio() {
    if (this.checkTodos.value && this.cargaArchivo) {
      this.cargaArchivo.eliminarDocumento();
    }
  }

  actualizaValidaciones() {
    const mediosSeleccionados: string[] = this.tipo.value;
    if (mediosSeleccionados.some((medio) => medio === TipoNotificacion.SMS)) {
      this.descripcion.setValidators([
        Validators.required,
        Validators.minLength(5),
        Validators.maxLength(this.limitesMensaje.SMS),
      ]);
    } else if (
      mediosSeleccionados.some((medio) => medio === TipoNotificacion.PUSH)
    ) {
      this.descripcion.setValidators([
        Validators.required,
        Validators.minLength(5),
        Validators.maxLength(this.limitesMensaje.PUSH),
      ]);
    } else {
      this.descripcion.setValidators([
        Validators.required,
        Validators.minLength(5),
        Validators.maxLength(this.limitesMensaje.CORREO),
      ]);
    }
    this.descripcion.updateValueAndValidity();
    this.formulario.updateValueAndValidity();
  }

  enviarPeticionTodos(body: ArchivoMensajesDTO) {
    this.cargando = true;
    this.apiService
      .getCurrentSesion()
      .then((sesion) => {
        const accessToken = sesion.getAccessToken();
        const jwt = accessToken.getJwtToken();
        const httpOptions = {
          headers: new HttpHeaders({
            ['Authorization']: 'Bearer ' + jwt,
          }),
        };
        const url =
          environment.endPointNotificacion + this.cargaArchivo.endPoint;
        return this.http
          .post<RespuestaEnvioNotificaciones>(url, body, httpOptions)
          .toPromise();
      })
      .then(
        (respuesta) => {
          this.cargando = false;
          this.cargaArchivo.cargaExcelExitosa(respuesta);
        },
        () => {
          this.cargando = false;
          this.cargaArchivo.mostrarModalError();
        }
      );
  }

  obtenerAcciones() {
    this.apiService.get('notificacion/acciones').then((acciones: [Accion]) => {
      this.acciones = acciones;
    });
  }
}
