import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ComercialComponent } from './comercial.component';
import { SeleccionarTipoEmpresaComponent } from './vista/seleccionar-tipo-empresa/seleccionar-tipo-empresa.component';
import { RouterModule, Routes } from '@angular/router';
import { RegistroEmpresaMexicoComponent } from './componentes/registro-empresa-mexico/registro-empresa-mexico.component';
import { RegistroEmpresaColombiaComponent } from './componentes/registro-empresa-colombia/registro-empresa-colombia.component';
import { MenuIzquierdoComponent } from './componentes/menu-izquierdo/menu-izquierdo.component';
import { NotificarComponent } from './vista/notificar/notificar.component';
import { SubirLayoutComponent } from './componentes/subir-layout/subir-layout.component';
import { NgxUploaderModule } from 'ngx-uploader';
import { HttpClientModule } from '@angular/common/http';
import { SharedModule } from '../../shared/shared.module';
import { PaisAmbienteGuard } from '../../core/guards/pais-ambiente.guard';
import { PaisAmbiente } from '../../shared/modelos/enums/paisAmbiente.enum';
import { InvitacionesEmpresaComponent } from '../../shared/componentes/invitaciones-empresa/invitaciones-empresa.component';
import { ReactiveFormsModule } from '@angular/forms';
import { MaterialModule } from '../material/material.module';

const routes: Routes = [
  {
    path: '',
    component: ComercialComponent,
    children: [
      {
        path: '',
        component: SeleccionarTipoEmpresaComponent,
        pathMatch: 'full',
      },
      {
        path: 'invitaciones-empresa',
        component: InvitacionesEmpresaComponent,
        canLoad: [PaisAmbienteGuard],
        data: { paises: [PaisAmbiente.MX] },
      },
      {
        path: 'notificaciones',
        component: NotificarComponent,
      },
      { path: '**', redirectTo: 'login' },
    ],
  },
];

@NgModule({
  declarations: [
    ComercialComponent,
    SeleccionarTipoEmpresaComponent,
    RegistroEmpresaMexicoComponent,
    RegistroEmpresaColombiaComponent,
    MenuIzquierdoComponent,
    NotificarComponent,
    SubirLayoutComponent,
  ],
  imports: [
    CommonModule,
    MaterialModule,
    ReactiveFormsModule,
    SharedModule,
    RouterModule.forChild(routes),
    NgxUploaderModule,
    HttpClientModule,
  ],
})
export class ComercialModule {}
