import { FormGroup } from '@angular/forms';
import { TipoIdentificacionEmpresa } from '../modelo/tipo-identificacion-empresa.enum';

export function identificacionColombiaValidador(
  tipoIdentificacion: string,
  identificacion: string
) {
  return (group: FormGroup): { [key: string]: any } => {
    const tipoIdentificacionControl = group.controls[tipoIdentificacion];
    const identificacionControl = group.controls[identificacion];
    if (tipoIdentificacionControl.value === TipoIdentificacionEmpresa.NIT) {
      const validNit = new RegExp(/^[0-9]{8,15}-{1}[0-9]{1}$/).test(
        String(identificacionControl.value).toLowerCase()
      );
      return validNit
        ? null
        : { invalidNIT: { valid: false, value: identificacionControl.value } };
    } else {
      const validCedula = new RegExp(/^[1-9][0-9]{5,10}$/).test(
        String(identificacionControl.value).toLowerCase()
      );
      return validCedula
        ? null
        : {
            invalidCedula: { valid: false, value: identificacionControl.value },
          };
    }
  };
}

export function identificacionMexicoValidador(
  tipoIdentificacion: string,
  identificacion: string
) {
  return (group: FormGroup): { [key: string]: any } => {
    const tipoIdentificacionControl = group.controls[tipoIdentificacion];
    const identificacionControl = group.controls[identificacion];
    if (
      tipoIdentificacionControl.value ===
      TipoIdentificacionEmpresa.REGISTRO_PATRONAL
    ) {
      const validRP = new RegExp(/^[a-zA-Z0-9]{11}[a-zA-Z0-9]{0,2}$/).test(
        String(identificacionControl.value).toLowerCase()
      );
      return validRP
        ? null
        : {
            invalidRegistroPatronal: {
              valid: false,
              value: identificacionControl.value,
            },
          };
    } else {
      const validRFC = /^([A-Z,Ã,&]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1])[A-Z|\d]{3})$/.test(
        String(identificacionControl.value)
      );
      return validRFC
        ? null
        : { invalidRFC: { valid: false, value: identificacionControl.value } };
    }
  };
}
