import { Validators, ValidatorFn } from '@angular/forms';

export const validarTextoSinNumero: ValidatorFn[] = [
  Validators.pattern(
    /^[a-zA-Z\u00C0-\u00D6\u00D8-\u00F6\u00F8-\u0178][a-zA-Z\u00C0-\u00D6\u00D8-\u00F6\u00F8-\u0178\.\- ']*$/
  ),
  Validators.minLength(2),
  Validators.maxLength(70),
];
